/* ÈÅÆÁΩ©Â±ÇÔºöÈì∫Êª°Â±èÂπïÔºåËêΩÂú®È°µÈù¢ÂÜÖÂÆπ‰πã‰∏ä„ÄÅÂ∫ïÊ†è‰πã‰∏ã */
.bn-overlay {
  position: fixed;
  inset: 0;
  background: transparent;       /* ‰∏çÁî®ÂçäÈÄèÊòéÔºå‰øùÊåÅÁÆÄÊ¥ÅÔºõË¶ÅÊöóÂπïÂèØÁî® rgba(0,0,0,.15) */
  z-index: 998;                  /* Â∞è‰∫éÂ∫ïÊ†è+Â≠êËèúÂçïÁöÑ z-index */
}

/* Â∫ïÈÉ®Ê†èÔºöÁ°Æ‰øùÂú®ÈÅÆÁΩ©‰πã‰∏äÔºåÂ≠êËèúÂçïÂÜçÈ´ò‰∏ÄÂ±Ç */
.bottom-nav {
  position: fixed;
  left: 0; right: 0; bottom: 0;
  height: 56px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  background: #fff;
  border-top: 1px solid #e5e7eb;
  z-index: 999;
}

/* Â≠êËèúÂçïÔºöÂõ∫ÂÆöÂÆö‰ΩçÂú®Â∫ïÊ†è‰∏äÊñπÔºåÂÆΩÂ∫¶=Â±èÂπï 1/4ÔºåÂπ∂Ê∞¥Âπ≥Â±Ö‰∏≠ */
.bottom-submenu {
  position: fixed;
  bottom: 56px;                 /* Ë¥¥‰ΩèÂ∫ïÊ†è‰∏äËæπÁºò */
  left: 50%;
  transform: translateX(-50%);  /* Â±Ö‰∏≠ */
  width: 35vw;                  /* üëà Â±èÂπïÂÆΩÂ∫¶ÁöÑ 1/4 */
  display: grid;
  grid-auto-flow: row;
  gap: 8px;
  padding: 10px 12px;
  background: rgba(255,255,255,0.88);
  backdrop-filter: blur(8px) saturate(1.02);
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  box-shadow: 0 -8px 24px rgba(0,0,0,0.08);
  z-index: 1000;                /* È´ò‰∫éÂ∫ïÊ†è‰∏éÈÅÆÁΩ© */
}

.bottom-submenu button {
  width: 100%;
  text-align: left;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  background: #fff;
  font-size: 14px;
}

.bottom-submenu button:active {
  transform: translateY(0);     /* ÁßªÂä®Á´ØÁÇπÂáªÂà´ÊäñÂä® */
}

/* === ‰ªÖË¶ÜÁõñÂ∫ïÊ†è‚ÄúÂõæÊ†á+ÊñáÂ≠ó‚ÄùÁöÑÊ†∑ÂºèÔºå‰∏çÊîπÂÆπÂô®‰∏éÂ≠êËèúÂçï === */
.bottom-nav .nav-item {
  /* Áªü‰∏Ä NavLink ‰∏é button ÁöÑËßÜËßâ */
  display: flex;
  flex-direction: column;    /* ÂõæÊ†áÂú®‰∏ä„ÄÅÊñáÂ≠óÂú®‰∏ã */
  align-items: center;
  justify-content: center;   /* ÂûÇÁõ¥Â±Ö‰∏≠ */
  gap: 3px;                  /* ÂõæÊ†á‰∏éÊñáÂ≠óÈó¥Ë∑ù */
  height: 100%;
  min-width: 68px;           /* Á¥ßÂáë */
  padding: 4px 8px;
  border-radius: 12px;
  background: none;
  border: 0;
  outline: none;
  text-decoration: none;
  cursor: pointer;

  /* È¢úËâ≤ & ËøáÊ∏°ÔºàÊù•Ëá™‰Ω†ÁöÑÊúÄÂêéÁâàÊú¨Ôºâ */
  font-size: 11px;
  color: var(--nav-text-dim, #64748b);
  transition: transform .12s ease, color .12s ease, background .12s ease, box-shadow .12s ease;
}

/* ÈÅøÂÖç SVG Âü∫Á∫øÂÅèÁßªÂØºËá¥ÁöÑ‚ÄúÁúãËµ∑Êù•‰∏çÂ±Ö‰∏≠‚Äù */
.bottom-nav .nav-item svg {
  display: block;
  line-height: 1;
}

/* ÊñáÊ°àË°åÈ´ò‰∏éÁªßÊâøÈ¢úËâ≤ */
.bottom-nav .nav-item .nav-label {
  margin-top: 2px;
  line-height: 1;
  font-size: 11px;
  color: inherit;
}

/* hover ÊïàÊûúÔºàËΩªÂæÆ‰∏äÊµÆ+ÊµÖÂ∫ïÔºâ */
.bottom-nav .nav-item:hover {
  color: var(--nav-text, #334155);
  transform: translateY(-1px);
  background: rgba(2, 6, 23, 0.02);
  box-shadow: inset 0 0 0 1px rgba(2, 6, 23, 0.05);
}

/* ÈÄâ‰∏≠/Â±ïÂºÄÊÄÅÈ´ò‰∫ÆÔºàNavLink.active / aria-current / Êàë‰ª¨ÁªôÁöÑ is-open Á±ªÔºâ */
.bottom-nav .nav-item.active,
.bottom-nav .nav-item[aria-current="page"],
.bottom-nav .nav-item.is-open {
  color: var(--nav-active, #0ea5e9);
  box-shadow: inset 0 0 0 1px var(--glow-soft, rgba(14,165,233,0.18));
}
/* ‚Äî‚Äî Áº©Â∞èÈÄâ‰∏≠/Â±ïÂºÄÈ´ò‰∫ÆÊ°ÜÂà∞Á∫¶‰∏ÄÂçä ‚Äî‚Äî */

/* ÂÖàÂÖ≥ÊéâÂéüÊù•ÁöÑÂ§ßÊ°ÜÔºàbox-shadow ÈÇ£‰∏™Ôºâ */
.bottom-nav .nav-item.active,
.bottom-nav .nav-item[aria-current="page"],
.bottom-nav .nav-item.is-open {
  box-shadow: none;           /* ÁßªÈô§‰πãÂâçÁöÑÂ§ßÊèèËæπ */
}

/* ÈúÄË¶ÅÂÆö‰ΩçÂÆπÂô® */
.bottom-nav .nav-item { position: relative; }

/* Áî®‰º™ÂÖÉÁ¥†Áîª‰∏Ä‰∏™Êõ¥Â∞èÁöÑÊ°ÜÔºöÂÆΩÈ´ò‚âàÂÆπÂô®ÁöÑ 56%ÔºàÁ∫¶‰∏ÄÂçäËßÜËßâÈù¢ÁßØÔºâ */
.bottom-nav .nav-item.active::after,
.bottom-nav .nav-item[aria-current="page"]::after,
.bottom-nav .nav-item.is-open::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 56%;
  height: 80%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  border: 1px solid var(--glow-soft, rgba(14,165,233,0.18));
  pointer-events: none;       /* ‰∏çÊå°ÁÇπÂáª */
}

/* hover Êó∂‰πüÁî®Â∞èÊ°ÜÔºàÊõ¥Ê∑°ÔºâÔºåÈÅøÂÖçÁúãËµ∑Êù•ÂèàÂ§ß‰∫Ü */
.bottom-nav .nav-item:hover::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 56%;
  height: 56%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  border: 1px solid rgba(2, 6, 23, 0.06);
  pointer-events: none;
}

/* ÂêåÊó∂Êää hover ÊïàÊûúÂº±ÂåñÔºåÈÅøÂÖç‚ÄúÂ§ßÁâáÂ∫ïËâ≤‚ÄùÊòæÂæóÊ°ÜÂæàÂ§ß */
.bottom-nav .nav-item:hover {
  background: transparent;
  transform: translateY(-0.5px);
  box-shadow: none;
}
