/* ===============================
   ÁßëÊäÄÈ£éÂç°ÁâáÔºàÁÅ∞ÁôΩÁéªÁíÉ + Ê∏êÂèòÊèèËæπ + ÂæÆÂä®ÊïàÔºâ
   ‚Äî‚Äî Êó† mask / Êó†ÂéÇÂïÜÂâçÁºÄÔºå‰ΩøÁî®Â§öÈáçËÉåÊôØÂÆûÁé∞Ê∏êÂèòËæπÊ°Ü
   =============================== */

:root {
  --ql-bg: rgba(255, 255, 255, 0.72);
  --ql-text: #0f172a;      /* slate-900 */
  --ql-sub: #64748b;       /* slate-500 */
  --ql-shadow: 0 8px 24px rgba(15, 23, 42, 0.08);
  --ql-shadow-hover: 0 14px 36px rgba(15, 23, 42, 0.12);
  --ql-radius: 16px;

  /* ÁßëÊäÄÊ∏êÂèòÔºà‰ΩéÈ•±ÂíåÔºâ */
  --ql-grad-1: #a5b4fc;  /* indigo-300 */
  --ql-grad-2: #93c5fd;  /* blue-300   */
  --ql-grad-3: #5eead4;  /* teal-300   */
  --ql-grad-opacity: 1;  /* ÂèØÂú® hover Êó∂ÂæÆË∞É */
}

/* ‰∏§ÂàóÂÆπÂô®ÔºàÁßªÂä®Á´ØËá™ÈÄÇÂ∫î‰∏ÄÂàóÔºâ */
.quicklink-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 14px;
  padding: 0 16px;
  margin: 10px 0 6px;
}
@media (max-width: 520px) {
  .quicklink-grid { grid-template-columns: 1fr; }
}

/* ---- Âü∫Êú¨Âç°ÁâáÁªìÊûÑ ---- */
.quicklink-card {
  position: relative;
  display: grid;
  grid-template-columns: 44px 1fr auto;
  align-items: center;
  gap: 12px;
  padding: 16px 18px;

  /* ÂÖ≥ÈîÆÔºöÁî®‰∏§Â±ÇËÉåÊôØÊ®°Êãü‚ÄúÊ∏êÂèòËæπÊ°Ü‚Äù */
  border: 1px solid transparent;                    /* ËæπÊ°ÜÈÄèÊòéÔºåÁî±Á¨¨‰∫åÂ±ÇËÉåÊôØÊâøÊãÖÊèèËæπ */
  border-radius: var(--ql-radius);
  background-image:
    linear-gradient(var(--ql-bg), var(--ql-bg)),    /* ËÉåÊôØÂ±ÇÔºàÁéªÁíÉÔºâ */
    linear-gradient(135deg,                          /* ËæπÊ°ÜÂ±ÇÔºàÊ∏êÂèòÔºâ */
      color-mix(in oklab, var(--ql-grad-1) 90%, transparent calc(100% - 90%)),
      color-mix(in oklab, var(--ql-grad-2) 90%, transparent calc(100% - 90%)),
      color-mix(in oklab, var(--ql-grad-3) 90%, transparent calc(100% - 90%))
    );
  background-origin: border-box;
  background-clip: padding-box, border-box;

  box-shadow: var(--ql-shadow);
  cursor: pointer;
  user-select: none;
  transition:
    transform 140ms ease,
    box-shadow 140ms ease,
    background-image 140ms ease;
  overflow: hidden; /* ËÆ©ÂÖâÊôï/È´òÂÖâË£ÅÂàáÂú®ÂúÜËßíÂÜÖ */
  backdrop-filter: blur(6px) saturate(1.02);
}

/* hover ‰∫§‰∫íÔºöÊèêÂçáÈò¥ÂΩ±Âπ∂ËÆ©ÊèèËæπÊõ¥Êòæ‰∏ÄÁÇπ */
.quicklink-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--ql-shadow-hover);
  --ql-grad-opacity: 1; /* ÈúÄË¶ÅÊõ¥ÊòéÊòæÊó∂ÂèØÊèêÈ´òÊ∏êÂèòÊµìÂ∫¶ */
}

/* ËßíËêΩÂÖâÊôïÔºàÊüîÂíå„ÄÅË∑üÈöè hoverÔºâ */
.quicklink-glow {
  position: absolute;
  width: 180px;
  height: 180px;
  right: -60px;
  top: -60px;
  background: radial-gradient(closest-side, rgba(148, 163, 184, 0.22), rgba(148, 163, 184, 0));
  filter: blur(8px);
  transition: transform 200ms ease, opacity 200ms ease;
  opacity: 0.6;
  pointer-events: none;
}
.quicklink-card:hover .quicklink-glow {
  transform: translate3d(-6px, 4px, 0) scale(1.02);
  opacity: 0.8;
}

/* ÂÄæÊñúÈ´òÂÖâÔºàÂæÆÂº±ÁßªÂä®Ôºâ */
.quicklink-sheen {
  position: absolute;
  left: -40%;
  top: -120%;
  width: 120%;
  height: 300%;
  background: linear-gradient(105deg, rgba(255,255,255,0), rgba(255,255,255,0.08) 40%, rgba(255,255,255,0));
  transform: rotate(8deg) translate3d(0,0,0);
  transition: transform 600ms ease;
  pointer-events: none;
}
.quicklink-card:hover .quicklink-sheen {
  transform: rotate(8deg) translateY(8%);
}

/* ÂõæÊ†áËÉ∂Âõä */
.quicklink-icon {
  width: 44px;
  height: 44px;
  border-radius: 14px;
  display: grid;
  place-items: center;
  background:
    linear-gradient(135deg, rgba(165,180,252,0.18), rgba(147,197,253,0.18));
  border: 1px solid rgba(203, 213, 225, 0.7);
  box-shadow: inset 0 0 0 1px rgba(255,255,255,0.36);
  transition: transform 140ms ease, box-shadow 140ms ease;
}
.quicklink-card:hover .quicklink-icon {
  transform: translateY(-1px);
  box-shadow: inset 0 0 0 1px rgba(255,255,255,0.5), 0 6px 14px rgba(99, 102, 241, 0.12);
}

.quicklink-content { min-width: 0; }

.quicklink-title {
  font-size: 14px;
  font-weight: 700;
  color: var(--ql-text);
  letter-spacing: 0.2px;
  line-height: 1.25;
}

.quicklink-desc {
  margin-top: 3px;
  font-size: 12px;
  color: var(--ql-sub);
  line-height: 1.3;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.quicklink-arrow {
  font-size: 18px;
  color: #94a3b8; /* slate-400 */
  transition: transform 140ms ease, color 140ms ease;
}
.quicklink-card:hover .quicklink-arrow {
  transform: translateX(2px);
  color: #64748b; /* slate-500 */
}
