.profile-container {
  padding: 12px;
  max-width: 420px;
  margin: 0 auto;
  color: #000000;
  background: rgb(255, 255, 255);

  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
  font-family: "Segoe UI", sans-serif;
}

.profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.profile-header h2 {
  font-size: 20px;
  font-weight: 600;
}

.connect-btn {
  padding: 6px 14px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(90deg, #00c6ff, #0072ff);
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

.connect-btn:hover {
  opacity: 0.85;
}

.wallet-info {
  background: rgba(255,255,255,0.05);
  padding: 6px 6px;
  border-radius: 12px;
  margin-bottom: 20px;
  font-size: 14px;
}

.token-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.token-item {
  display: flex;
  align-items: center;
  background: rgba(255,255,255,0.05);
  padding: 10px 14px;
  border-radius: 12px;
}

.token-icon {
  width: 28px;
  height: 28px;
  margin-right: 10px;
  border-radius: 50%;
  background: rgba(255,255,255,0.1);
}

.token-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.token-symbol {
  font-size: 15px;
  font-weight: 500;
}

.token-balance {
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
